import React, { useState } from 'react';
import { LoginData } from '../../../Data/SignUpData';
import VisibilityOffIcon from '@mui/icons-material/VisibilityOff';
import RemoveRedEyeIcon from '@mui/icons-material/RemoveRedEye';

const Login = () => {
    const [formData, setFormData] = useState({
        email: '',
        password: ''
    });
    const [errors, setErrors] = useState({});
    const [showPassword, setShowPassword] = useState(false); // State to toggle password visibility

    const handleChange = (e) => {
        const { name, value } = e.target;
        setFormData(prevState => ({
            ...prevState,
            [name]: value
        }));
    };

    const handleTogglePasswordVisibility = () => {
        setShowPassword(!showPassword); // Toggle the state
    };

    const handleSubmit = (e) => {
        e.preventDefault();
        if (!formData.email || !formData.password) {
            alert("Field cannot be empty!");
            return;
        }
        if (!/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(formData.email)) {
            alert("Invalid email format!");
            return;
        }
        console.log('Form submitted:', formData);
        alert('Login successful!');
    };

    return (
        <form onSubmit={handleSubmit}>
            {LoginData.map((field, index) => (
                <div className="form-group" key={index}>
                    <label htmlFor={field.name}>{field.label}</label>
                    <div style={{ position: 'relative' }}>
                        <input
                            type={field.name === "password" && !showPassword ? "password" : "text"}
                            id={field.name}
                            name={field.name}
                            placeholder={field.placeholder}
                            value={formData[field.name]}
                            onChange={handleChange}
                            className="text-input"
                        />
                        {field.name === "password" && (
                            <span onClick={handleTogglePasswordVisibility} style={{ position: 'absolute', right: '10px', top: '8px', cursor: 'pointer' }}>
                                {showPassword ? <VisibilityOffIcon /> : <RemoveRedEyeIcon />}
                            </span>
                        )}
                    </div>
                    {errors[field.name] && <div className="error">{errors[field.name]}</div>}
                </div>
            ))}
            <button type="submit" className="submit-btn">Sign In</button>
        </form>
    );
};

export default Login;
