import React from 'react';
import './App.css';
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import Header from './components/home/Header';
import Footer from './components/home/Footer';
import HomePages from './components/pageContent/HomePages';
import MainDash from './components/PractitionerDash/MainDash/MainDash';
import AllRequests from './components/PractitionerDash/AllRequests';

import Sidebar from './components/PractitionerDash/Sidebar/Sidebar';

function App() {
  return (
    <Router>
      <Switch>
        {/* Route for the Homepage - only Header and Footer included */}
        <Route path="/" exact>
          <Header />
          <HomePages />
          <Footer />
        </Route>

        {/* Route for Dashboard components which only include Sidebar and MainDash */}
        <Route path="/dash" exact>
          <div className="dashboard-container">
            <Sidebar />
            <MainDash />
          </div>
        </Route>

        {/* Route for Requests which only include Sidebar and Requests */}
        <Route path="/request" exact>
          <div className="dashboard-container">
            <Sidebar />
            <AllRequests />
          </div>
        </Route>
      </Switch>
    </Router>
  );
}

export default App;
